import BHLink from './components/link/src/link.vue'
import BHButton from './components/button/src/button.vue'

//导入css
import './theme-chalk/src/index.scss'

const components = {
	BHLink,
	BHButton,
};


const install = (app:any) => {
	Object.values(components).forEach(comp => {
		app.component(comp.name, comp);
	});
};

const ygbh = {
	install: install
};

export default ygbh;

export {components};

export { install };